
.food li{
  margin:0 0 .3125rem /* 5/16 */ .3125rem /* 5/16 */;
  padding: .625rem /* 10/16 */ 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  /* background-color: skyblue; */
}
.food li img{
  width: 5.625rem /* 90/16 */;
  height: 5.625rem /* 90/16 */;
  border-radius: .1875rem /* 3/16 */;
  margin-right: .3125rem /* 5/16 */;
}
.food_right{
  width: calc( 100% - 5.625rem - .3125rem);
}
.food_right .food_name{
  width: 100%;
  font-size: .875rem /* 14/16 */;
  font-weight: 600;
}
.food_right .food_jieshao{
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: #999;
  font-size: .6875rem /* 11/16 */;
  width: 100%;
}
.food_right .chengji{
  color: #999;
  font-size: .6875rem;
}
.food_right .main{
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: .625rem /* 10/16 */;
  padding-right: .625rem /* 10/16 */;
}
.food_right .main .btn svg{
  width: 1.25rem /* 20/16 */;
  height: 1.25rem /* 20/16 */;
}
.main .price{
  font-size: 1rem /* 16/16 */;
  color: rgb(255, 83, 57);
}
.main .price small{
    font-size: .625rem /* 10/16 */;
}
.main .btn{
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.main .btn span{
  padding: 0 .3125rem /* 5/16 */;
}
.main .btn span.count{
  font-size: .875rem /* 14/16 */;
}



