html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
.router-link-active {
  color: #0089dc;
}
.router-link-active svg path {
  fill: #0089dc;
}
body {
  background-color: #f5f5f5;
  height: 100%;
}
li {
  list-style: none;
}
#app {
  width: 100%;
  padding-bottom: 30px;
}
#footer {
  width: 100%;
  position: fixed;
  bottom: 0px;
  z-index: 10;
  box-shadow: 0 -0.266667vw 0.533333vw rgba(0, 0, 0, 0.1);
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
#footer li {
  width: 25%;
  text-align: center;
  background-color: white;
  cursor: pointer;
  font-size: 0.625rem /* 10/16 */;
  padding: 0.3125rem /* 5/16 */ 0px;
}
#footer_icon {
  width: 1.25rem /* 20/16 */;
  height: 1.25rem /* 20/16 */;
}
.vux-slider {
  background-color: #fff;
  text-align: center;
  overflow: unset !important;
}
.vux-swiper-item .kind_swiper {
  width: 20%;
  height: 50%;
  float: left;
  box-sizing: border-box;
  /* position: absolute; */
}
.vux-swiper-item div img {
  width: 60%;
}
.vux-indicator .vux-icon-dot {
  width: 0.625rem !important;
  /* 10/16 */
  height: 0.125rem !important;
  /* 2/16 */
}
.vux-slider > .vux-indicator,
.vux-slider .vux-indicator-right {
  bottom: 0px !important;
}
.vux-swiper-desc {
  background-image: unset !important ;
}
.vux-tab-selected {
  font-weight: 700;
}
.vux-card-content {
  width: 100% !important;
}
.weui-panel {
  margin-top: 0 !important;
}
.vux-tab-item {
  font-size: 0.875rem /* 14/16 */ !important;
}
#first_tab .vux-tab-container {
  height: 3.25rem !important;
}
#first_tab .vux-tab-container .vux-tab {
  height: 3.25rem /* 52/16 */ !important;
}
#first_tab .vux-tab .vux-tab-item {
  line-height: 3.25rem;
}
#store_tab .vux-tab-container {
  height: 3.25rem !important;
}
#store_tab .vux-tab-container .vux-tab {
  height: 3.25rem /* 52/16 */ !important;
}
#store_tab .vux-tab .vux-tab-item {
  line-height: 3.25rem;
}
#store_tab {
  padding-top: 3.25rem !important;
}



